#bodyDiv{
    margin-top: 85px;
}
.container {
    max-width: 1200px;
    margin: auto;
    padding: 20px;
}

h1 {
    text-align: Left;
    color: #004d40;
    margin-bottom: 20px;
}

h2 {
    color: #00796b;
    margin: 20px 0;
    font-size: 1.5em;
    border-bottom: 2px solid #00796b;
    padding-bottom: 10px;
}

h3 {
    margin: 20px 0 10px;
    color: #004d40;
}

p {
    margin: 2px 0;
}

.expander {
    background-color: #ffffff;
    border: 1px solid #dbe2e8;
    border-radius: 8px;
    margin-top: 20px;
    /* padding: 15px; */
    cursor: pointer;
    transition: background-color 0.3s, box-shadow 0.3s;
    background: rgba(13, 150, 175, 0.1);
}

.expander:hover {
    /* background-color: #f1f8f4; */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.expander ul {
    list-style-type: square;
    padding-left: 30px;
}

.expander-content {
    /* display: none; */
    margin-top: 10px;
}

iframe {
    width: 100%;
    border: none;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-top: 10px;
}

.read-more {
    color: #00796b;
    cursor: pointer;
    font-weight: bold;
    display: inline-block;
    margin-top: 10px;
    text-decoration: none;
    padding-left: 19px;
}
#mainHeader{
    background-image: url("bcg_image.jfif");
    border-radius: 10px;
    padding-left: 7px;
    padding-top: 30px;
}
#mainHeader h1, h2{
    color: white;
}
.topDiv{
    padding-top: 0px
}
#hiv-more > p:first-of-type {
    font-size: 1.2rem;
    font-weight:600; /* Slightly smaller than the main paragraph */
    font-style: normal;    /* Makes the text italic */
    color: #0c5b7f;
    padding: 0.5rem;           /* Changes color to a subtle grey */
    margin-top: 0.5rem;    /* Adds some space between the main and sub-paragraph */
    line-height: 1.6;    /* Adjusts line height for better readability */
}
#hiv-more >p.sub-paragraph {
    font-size: 1rem;
    font-weight: 400;       /* Slightly smaller than the main paragraph */
    font-style: normal;    /* Makes the text italic */
    color: #666;           /* Changes color to a subtle grey */
    margin-top: 0.5rem;    /* Adds some space between the main and sub-paragraph */
    line-height: 1.5;      /* Ensures readability */
}
