#headerDiv{
  position: fixed;  /* This makes the header fixed */
  top: 0;           /* Sticks it to the top */
  width: 100%;      /* Ensures it stretches across the full width */
  z-index: 1000;    /* Ensures it stays above other content */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Optional: adds a slight shadow */
  .toolBarDiv{
    display: flex;
    justify-content: space-between;
    .headerIcons{
        display: flex;
        gap: 20px;
        >svg{
            cursor: pointer;
        }
    }
  }
}